* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .main_container {
    display: flex;
  }
  
  .sidebar {
    background: white;
    color: #0070c2;
    height: 100vh;
    width: 16vw;
    transition: all 0.5s;
    overflow-y: auto;
  }
  .contentUI{
    transition: all 0.5s;
  }
  
  .contentUI ::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 1px;
    height: 10px;
}

  .top_section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0px;
    background-color: #F2F7FC !important;
  }
  
  .menu_heading_text {
    white-space: nowrap;
    font-size: 15px;
  }
  
  .bars {
    width: 30px;
    cursor: pointer;
  }
  
  .components {
    display: flex;
    flex-direction: column;
  }
  
  .link {
    display: flex;
    padding: 5px 0px;
    transition: 0.5s all;
    background-color: rgb(234 243 255);
  }
  
  
  .link:hover > div {
    color: #fff !important;
    transition: 0.5s all;
  }
  
  main {
    padding: 15px;
    padding-left: 15px;
    width: 82vw;
    height: 100vh;
    background-color: #FFFFFF;
    /* border: 1px solid rgba(34, 36, 38, 0.15);
    box-shadow: 3px 1px 2px 0 rgb(34 36 38 / 15%); */
  }
  .menu_item_text {
    color: #404040 !important;
    white-space: nowrap;
    font-size: 10px;
    font-weight: 600;
  }
  .submenu:hover {
    color: #fff !important;
    background: #0070c2;
  }
  .submenu:hover > div > div {
    color: #fff !important;
    background: #0070c2;
  }
  .submenu:hover > div > i {
    color: #fff !important;
    background: #0070c2;
  }
  .submenu_items:hover > div {
    color: #fff !important;
  }
  .submenu_items:hover > i {
    color: #fff !important;
  }
  
  .sublink_menu_text {
    color: #404040 !important;
    white-space: nowrap;
    font-size: 10px;
    font-weight: 600;
  }
  .nav_active {
    color: #fff !important;
    background: #0070c2;
  }
  
  .nav_active > div {
    color: #fff !important;
  }
  
  .submenu {
    display: flex;
    justify-content: space-between;
    color: #404040;
    grid-gap: 10px;
    gap: 10px;
    padding: 5px 10px;
    /* border-right: 4px solid transparent; */
    cursor: pointer;
  }
  
  .submenu_items {
    display: flex;
    justify-content: space-between;
    grid-gap: 10px;
    gap: 10px;
    padding: 1px 7px;
    font-size: 10px;
  }
  
  .submenu_container {
    display: flex;
    flex-direction: column;
    height: auto;
    transition: all 2s !important;
    cursor: pointer;
    background-color: #f7f8f7;
  }
  
  .submenu_container .link {
    padding-left: 35px;
  }
  
  
  
  .iconLarge {
    font-size: 16px !important;
  }
  .iconSmall {
    font-size: 14px !important;
  }
  
  a{
    color: #0070c2 !important;
  }

  .drRptCell1{
    width:220px !important;
    flex-shrink: 0 !important;
    text-align: left;
  }

  .drRptCell2{
    width: 100px !important;
    flex-shrink: 0 !important;
    text-align: left;
  }
